<template>
  <div class="col-md-12">
    <div class="card card-container">
      <img
        id="profile-img"
        src="//ssl.gstatic.com/accounts/ui/avatar_2x.png"
        class="profile-img-card"
      />
      <form name="form" @submit.prevent="handleRegister">
        <div v-if="!successful">
          <div class="form-group">
            <label for="username">Username</label>
            <input
              v-model="user.username"
              v-validate="'required|min:3|max:20'"
              type="text"
              class="form-control"
              name="username"
            />
            <div
              v-if="submitted && errors.has('username')"
              class="alert-danger"
            >{{errors.first('username')}}</div>
          </div>

          <div class="form-group">
            <label for="firstName">First name</label>
            <input
              v-model="user.firstName"
              v-validate="'required|min:3|max:20'"
              type="text"
              class="form-control"
              name="firstName"
            />
            <div
              v-if="submitted && errors.has('firstName')"
              class="alert-danger"
            >{{errors.first('firstName')}}</div>
          </div>

          <div class="form-group">
            <label for="lastName">Last name</label>
            <input
              v-model="user.lastName"
              v-validate="'required|min:3|max:20'"
              type="text"
              class="form-control"
              name="lastName"
            />
            <div
              v-if="submitted && errors.has('lastName')"
              class="alert-danger"
            >{{errors.first('lastName')}}</div>
          </div>


          <div class="form-group">
            <label for="status">Status</label>
            <select v-model="user.status"
                    v-validate="'required|min:1|max:10'"
                    type="text"
                    class="form-control"
                    name="status">
              <option style="color: green;" value="active">Active</option>
              <option style="color: red;" value="disable">Inactive</option>
            </select>
            <div
              v-if="submitted && errors.has('status')"
              class="alert-danger"
            >{{errors.first('status')}}</div>
          </div>

          <div class="form-group">
            <label for="email">Email</label>
            <input
              v-model="user.email"
              v-validate="'required|email|max:50'"
              type="email"
              class="form-control"
              name="email"
            />
            <div
              v-if="submitted && errors.has('email')"
              class="alert-danger"
            >{{errors.first('email')}}</div>
          </div>
          <div class="form-group">
            <label for="password">Password</label>
            <input
              v-model="user.password"
              v-validate="'required|min:6|max:40'"
              type="password"
              class="form-control"
              name="password"
            />
            <div
              v-if="submitted && errors.has('password')"
              class="alert-danger"
            >{{errors.first('password')}}</div>
          </div>

          <div class="form-group">
            <button class="addNewUser"><span>Add new driver</span></button>
          </div>
        </div>
      </form>

      <div
        v-if="message"
        class="alert"
        :class="successful ? 'alert-success' : 'alert-danger'"
      >{{message}}</div> 

      <div v-if="lastRegiserUser" class="linkToUser animate__animated animate__fadeInDown">
        <router-link style="text-decoration: none; color: inherit;" :to="'user/' + lastRegiserUser._id">
          <span>Go to profile {{ lastRegiserUser.firstName }} {{ lastRegiserUser.lastName }}</span>
        </router-link>  
      </div>

    </div>
  </div>
</template>

<script>
const url = 'http://localhost:3000/'
import User from '../models/user';
import axios from 'axios';
import authHeader from '../services/auth-header';

export default {
  name: 'Register',
  data() {
    return {
      user: new User('', '', '', '', '', ''),
      submitted: false,
      successful: false,
      lastRegiserUser: '',
      message: '',
    };
  },
  methods: {
    handleRegister() {
      this.message = '';
      this.submitted = true;
      this.$validator.validate().then(isValid => {
        if (isValid) {
          this.$store.dispatch('auth/register', this.user).then(
            data => {
              this.message = data.message;
              this.successful = true;
              this.returnLastUser()
            },
            error => {
              this.message =
                (error.response && error.response.data) ||
                error.message ||
                error.toString();
              this.successful = false;
            }
          );
        }

      });
    },
    returnLastUser() {
      axios
          .get(url + 'admin/', { headers: authHeader()})
            .then(response => {
              return response.data
            })
            .then(data => {
              var result = this.lastRegiserUser = data;
              return result
            })
            .then(result => {
              this.lastRegiserUser = result.slice(-1)[0]
            })
            .catch(err => {
               console.log(err)
            })
      }
  }
};
</script>

<style scoped>
.linkToUser{
    display: inline-block;
    border-radius: 4px;
    background-color: rgb(113 171 127 / 100%);
    border: none;
    color: #FFFFFF;
    text-align: center;
    font-size: 16px;
    padding-top: 7px;
    height: 40px;
    transition: all 0.5s;
    cursor: pointer;
    margin-top: 15px;
}
.linkToUser span {
  cursor: pointer;
  display: inline-block;
  position: relative;
  transition: 0.5s;
}
.linkToUser span:after {
  content: '\00bb';
  position: absolute;
  opacity: 0;
  top: 0;
  right: -20px;
  transition: 0.5s;
}
.linkToUser:hover span {
  padding-right: 25px;
}
.linkToUser:hover span:after {
  opacity: 1;
  right: 0;
}
.addNewUser{
    display: inline-block;
    border-radius: 4px;
    background-color: rgb(113 171 127 / 100%);
    border: none;
    color: #FFFFFF;
    text-align: center;
    font-size: 16px;
    height: 40px;
    transition: all 0.5s;
    cursor: pointer;
    margin-top: 15px;
    width: 100%;
}
.addNewUser span {
  cursor: pointer;
  display: inline-block;
  position: relative;
  transition: 0.5s;
}
.addNewUser span:after {
  content: '\00bb';
  position: absolute;
  opacity: 0;
  top: 0;
  right: -20px;
  transition: 0.5s;
}
.addNewUser:hover span {
  padding-right: 25px;
}
.addNewUser:hover span:after {
  opacity: 1;
  right: 0;
}
label {
  display: block;
  margin-top: 10px;
}

.card-container.card {
  max-width: 350px !important;
  padding: 20px 20px;
  border-radius: 20px;
}

.card {
  background-color: #f7f7f7;

  padding: 20px 25px 30px;
  margin: 0 auto 25px;
  margin-top: 50px;
  -moz-border-radius: 2px;
  -webkit-border-radius: 2px;
  border-radius: 2px;
  -moz-box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
  -webkit-box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
}

.profile-img-card {
  width: 96px;
  height: 96px;
  margin: 0 auto 10px;
  display: block;
  -moz-border-radius: 50%;
  -webkit-border-radius: 50%;
  border-radius: 50%;
}
</style>